.rf-tile__img img {
  object-fit: cover;
  object-position: 50% 50%; }

.rf-tile__link {
  text-decoration: none; }

.rf-tile {
  color: var(--g700); }

.rf-tile {
  position: relative;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  background-color: var(--beige); }
  .rf-grid-row .rf-tile {
    height: 100%; }
  .rf-tile__link {
    position: static;
    display: inline-flex;
    width: 100%; }
    a.rf-tile__link {
      box-shadow: none; }
      a.rf-tile__link[target="_blank"]::after {
        content: none;
        margin-left: 0; }
    .rf-tile__link::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0; }
    :root .rf-tile__link:focus {
      outline-offset: 9999px; }
  .rf-tile__img {
    position: relative;
    margin: 3rem 3rem 0;
    flex: 0 0 auto; }
    .rf-tile__img::before {
      display: block;
      content: '';
      width: 100%;
      padding-bottom: 100%; }
    .rf-tile__img img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%; }
  .rf-tile__body {
    padding: 1rem;
    flex-grow: 1; }
  .rf-tile__img + .rf-tile__body {
    padding: 1.5rem 1rem; }
  .rf-tile__title {
    margin: 0; }
    :root .rf-tile__title {
      font-size: 1rem;
      line-height: 1.5;
      font-weight: bold; }
    .rf-tile__title + .rf-tile__desc {
      margin: 0.5rem 0 0;
      font-size: 0.875rem;
      line-height: 1.5; }
  .rf-tile__desc {
    margin: 0; }
  .rf-tile::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 0.25rem;
    left: 0;
    bottom: 0;
    background-color: var(--bf500); }
  .rf-tile:focus-within {
    outline: 2px solid var(--focus);
    outline-offset: 2px;
    z-index: var(--focus-z-index); }
  @media (min-width: 48em) {
    .rf-tile--horizontal {
      flex-direction: row; }
      .rf-tile--horizontal .rf-tile__img {
        margin: 2rem 0 2rem 2rem;
        flex-grow: 1;
        flex-basis: auto;
        width: auto; }
      .rf-tile--horizontal .rf-tile__body {
        padding: 2rem;
        flex-basis: 70%; } }
